/**
 * @author cqb 2016-04-20.
 * @module Table
 */

import React from 'react';
import classNames from 'classnames';
import BaseComponent from '../core/BaseComponent';
import PropTypes from 'prop-types';
import UUID from '../utils/UUID';
import Header from './Header';
import Body from './Body';
import {List} from 'immutable';
import './Table.less';


/**
 * Table 
 * @class Table
 * @extend BaseComponent
 */
class Table extends BaseComponent {
    static displayName = 'Table';

    static defaultProps = {
        data: [],
        sortMode: 'single'
    };

    constructor (props) {
        super(props);

        this.orignData = List(props.data).toJS();

        this.data = this._rebuildData(this.orignData);

        this.addState({
            data: this.data || [],
            columns: props.columns
        });

        this.checkboxes = {};

        this._index = 1;
    }

    _rebuildData (data) {
        if (data && data.length) {
            return data.map((item) => {
                return {
                    key: item.id || UUID.v4(),
                    data: item
                };
            });
        }
        return data;
    }

    /**
     * 
     * @method setData
     * @param data {Array} 
     */
    setData (data) {
        this.orignData = List(data).toJS();
        this.data = this._rebuildData(this.orignData);
        this._index = 1;
        this.setState({data: this.data});
        // checkbox
        this.refs.header.check(false);
    }

    /**
     * 
     * @returns {*}
     */
    getData () {
        return this.state.data;
    }

    /**
     * 
     */
    getColumns () {
        return this.state.columns;
    }

    /**
     * 
     * @param row
     */
    addRow (row) {
        const data = List(this.getData());
        const newData = data.push({
            key: row.id || UUID.v4(),
            data: row
        }).toJS();

        this.setState({data: newData});
        this.refreshHeaderCheckBox(null, false);
    }

    /**
     * 
     * @param index
     */
    removeRow (index) {
        const data = List(this.getData());
        const newData = data.delete(index).toJS();
        this.setState({data: newData}, () => {
            this.refreshHeaderCheckBox(null, true);
        });
    }

    /**
     * 
     * @param field
     * @param value
     */
    removeRows (field, value) {
        const data = List(this.getData());
        const newData = data.filter((item) => {
            return item.data[field] !== value;
        }).toJS();
        this.setState({data: newData}, () => {
            this.refreshHeaderCheckBox(null, true);
        });
    }

    /**
     * 
     * @method resetData
     * @param data {Object} {header: [], data: []}
     */
    resetData (data) {
        if (data.data) {
            this.orignData = List(data.data).toJS();
            this.data = this._rebuildData(this.orignData);
            this._index = 1;
            this.setState({columns: data.columns, data: this.data});
            // checkbox
            this.refs.header.check(false);
        }
    }

    /**
     * 
     * @param checked
     */
    checkedAll (checked) {
        for (const key in this.checkboxes) {
            const row = this.checkboxes[key]['row'];
            row.check(checked);
        }
    }

    /**
     * 
     * @param field
     * @param value
     */
    checkRow (field, value) {
        for (const key in this.checkboxes) {
            const row = this.checkboxes[key]['row'];
            if (row.getData()[field] == value) {
                row.check(true);
            }
        }
    }


    /**
     * 
     * @param field
     * @param value
     */
    unCheckRow (field, value) {
        for (const key in this.checkboxes) {
            const row = this.checkboxes[key]['row'];
            if (row.getData()[field] == value) {
                row.check(false);
            }
        }
    }

    /**
     * checkbox
     * @param key
     * @param data
     */
    bindCheckBox (key, data) {
        this.checkboxes[key] = data;
    }

    /**
     * checkbox
     * @param key
     */
    unBindCheckBox (key) {
        delete this.checkboxes[key];
    }

    /**
     * 
     */
    getAllChecked () {
        const data = []; const rows = [];
        for (const key in this.checkboxes) {
            const row = this.checkboxes[key]['row'];
            if (row.isChecked()) {
                data.push(row.getData());
                rows.push(row);
            }
        }

        return {
            data,
            rows
        };
    }

    /**
     * checkbox
     * @param key
     * @param checked
     */
    refreshHeaderCheckBox (key, checked) {
        if (!checked) {
            this.refs.header.check(checked);
        } else {
            let isAllChecked = true;
            for (const key in this.checkboxes) {
                const row = this.checkboxes[key]['row'];
                if (!row.isChecked()) {
                    isAllChecked = false;
                    break;
                }
            }
            
            this.refs.header.check(isAllChecked);
        }
    }

    /**
     * 
     * @param  {[type]} column [description]
     * @param  {[type]} type   [description]
     * @param  {[type]} sorts  [description]
     * @return {[type]}        [description]
     */
    onSort (column, type, sorts) {
        if (this.props.onSort) {
            this.props.onSort(column, type, sorts);
        }
        this.emit('sort', column, type, sorts);
    }

    render () {
        const className = classNames('cm-table', this.props.className, {
            'table-bordered': this.props.bordered,
            'table-striped': this.props.striped,
            'table-hover': this.props.hover
        });
        return (
            <div className='table-responsive'>
                <table className={className} style={this.props.style}>
                    <Header sortMode={this.props.sortMode} ref='header' columns={this.state.columns} table={this} />
                    <Body ref='body' data={this.state.data} columns={this.state.columns} table={this} />
                </table>
            </div>
        );
    }
}


Table.Header = Header;

Table.Body = Body;


Table.propTypes = {
    /**
     * 
     * @attribute data
     * @type {Array}
     */
    data: PropTypes.array,
    /**
     * 
     * @attribute header
     * @type {Array}
     */
    header: PropTypes.array,
    /**
     * 
     * @attribute width
     * @type {String}
     * @default auto/100%
     */
    width: PropTypes.string,
    /**
     * 
     * @attribute height
     * @type {String}
     * @default auto
     */
    height: PropTypes.string,
    /**
     * 
     * @attribute bordered
     * @type {Boolean}
     * @default false
     */
    bordered: PropTypes.bool,
    /**
     * 
     * @attribute striped
     * @type {Boolean}
     * @default false
     */
    striped: PropTypes.bool,
    /**
     * 
     * @attribute hover
     * @type {Boolean}
     * @default false
     */
    hover: PropTypes.bool
};

export default Table;



